import { Component, OnInit } from '@angular/core';
import { PricingService } from '../../../pricing.service';
import { SelectItem } from 'primeng/components/common/selectitem';
import { PricingComponent } from '../../pricing.component';
import { ChartModule } from 'primeng/primeng';

@Component({
  selector: 'app-commodities-chart',
  templateUrl: './commodities-chart.component.html',
  styleUrls: ['./commodities-chart.component.css'],
  providers: [PricingService]
})
export class CommoditiesChartComponent implements OnInit {


  colorStack: Array<string>
  
    data: any
    unfilteredDataSets: any[] = new Array()
  
    currencies: SelectItem[] = new Array();
    selectedCurrency: string
    
  
    constructor(private pricingService: PricingService) {
      this.data = {
        labels: [],
        datasets: []
      }
     }
  
    ngOnInit() {
      this.colorStack = PricingComponent.COLORS.slice()
      this.loadCurves()
    }
  
    private loadCurves(){
      this.pricingService.getCommodities()
        .then(result => {
          this.data.labels = result.curves[0].curve.map(e => e.date)
          result.curves.forEach(element => {
            // TODO: expression curves are ignored for now. Need to be handled on the server
            if (element['@class'] === ".ExpressionCurve"){
              return
            }
  
            this.unfilteredDataSets.push({
              currency: element.currency,
              label: element.name,
              data: element.curve.map(e => e.value),
              fill: false,
              borderColor: this.colorStack.pop()
            })
          });
  
          this.getCurrencies(result.curves);
        })
    }
  
    currencyChange(){
      this.filterCurrencies(this.selectedCurrency)
    }
  
    private getCurrencies(curves: any){
      let currencies = new Set()
  
      curves.forEach(element => {
        currencies.add(element.currency)
      });
  
      let currencyList = Array.from(currencies);
      for (var i = 0; i < currencyList.length; i++){
        if (i == 0){
          this.selectedCurrency = currencyList[i]
          this.filterCurrencies(currencyList[i])
        }
        this.currencies.push({label: currencyList[i], value:currencyList[i] })
      }
    }
  
    private filterCurrencies(currency: string){
      this.data = {
        datasets: this.unfilteredDataSets.filter(e => e.currency === currency),
        labels: this.data.labels
      }
    }

}



