import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
 
import { DistanceMatrixComponent } from "./distance-matrix/distance-matrix.component";
import { PortsComponent } from "./ports/ports.component";
import { PortDetailComponent } from "./port-detail/port-detail.component";
import { PricingComponent } from './pricing/pricing.component';
import { CommoditiesComponent } from './pricing/commodities/commodities.component';
import { CurrenciesComponent } from './pricing/currencies/currencies.component';
import { BaseFuelComponent } from './pricing/basefuel/basefuel.component';
import { CharterComponent } from './pricing/charter/charter.component';
import { CommoditiesEditorComponent } from './pricing/commodities/commodities-editor/commodities-editor.component';
import { CommoditiesChartComponent } from './pricing/commodities/commodities-chart/commodities-chart.component';
import { CurrenciesChartComponent } from './pricing/currencies/currencies-chart/currencies-chart.component';
import { CurrenciesEditorComponent } from './pricing/currencies/currencies-editor/currencies-editor.component';

const routes: Routes = [
  { path: '', redirectTo: '/distances', pathMatch: 'full' },
  { path: 'distances',  component: DistanceMatrixComponent },
  { path: 'distances/:version',  component: DistanceMatrixComponent },
  { path: 'ports/:id', component: PortDetailComponent },
  { path: 'ports',     component: PortsComponent },
  { path: 'pricing/:version',     component: PricingComponent,
    children: [
      { path: '', redirectTo: 'commodities', pathMatch: 'full'}, 
      { path: 'commodities',     component: CommoditiesComponent, children: [
        { path: '', redirectTo: 'chart', pathMatch: 'full'},
        {path: 'chart', component: CommoditiesChartComponent},
        {path: 'editor', component: CommoditiesEditorComponent}
        ] 
      },
      { path: 'currencies',     component: CurrenciesComponent, children: [
        { path: '', redirectTo: 'chart', pathMatch: 'full'},
        {path: 'chart', component: CurrenciesChartComponent},
        {path: 'editor', component: CurrenciesEditorComponent}
      ] },
      { path: 'basefuel',     component: BaseFuelComponent },
      { path: 'charter',     component: CharterComponent }
    ]
   }
];
 
@NgModule({
  imports: [ RouterModule.forRoot(routes, { useHash: true }) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}


