import { Injectable, Optional, Inject } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { BASE_PATH } from '../../generated/ports-distances/variables';

@Injectable()
export class PricingService {

  private basePath = "http://localhost:8096"

  constructor(private http: HttpClient, @Optional() @Inject(BASE_PATH) basePath: string) { 
    if (basePath) {
      this.basePath = basePath;
    }
  }

  public getCurrencies(version?: string): Promise<any>{
    return this.getSpecific(`${this.basePath}/currencies`, version)
  }
  
  public getCommodities(version?: string): Promise<any>{
    return this.getSpecific(`${this.basePath}/commodities`, version)
  }

  public getBaseFuel(version?: string): Promise<any>{
    return this.getSpecific(`${this.basePath}/basefuel`, version)
  }

  public getCharter(version?: string): Promise<any>{
    return this.getSpecific(`${this.basePath}/charter`, version)
  }

  private getSpecific(endPoint: string, version?: string): Promise<any>{
    let urlParams = new HttpParams()
    if (version != undefined){
      urlParams = urlParams.append('v', version);
    }
    return this.http.get<Array<any>>(endPoint, { params: urlParams }).toPromise().then(response => {
      return this.getCurves(response.map(e => e.name), version)
    })
  }

  /**
   * Bulk retrival of curves
   * @param curves 
   */
  public getCurves(curves: Array<string>, version?: string): Promise<any>{
    let urlParams = curves.reduce((params: HttpParams, element) => params.append("curve", element), new HttpParams())
    if (version != undefined){
      urlParams = urlParams.append('v', version);
    }
    return this.http.get(`${this.basePath}/bulk`, {
      params: urlParams
    }).toPromise()
  }
}



