import { Component, OnInit } from '@angular/core';
import { PricingService } from '../../../pricing.service';

@Component({
  selector: 'app-commodities-editor',
  templateUrl: './commodities-editor.component.html',
  styleUrls: ['./commodities-editor.component.css'],
  providers: [PricingService]
})
export class CommoditiesEditorComponent implements OnInit {

  curves: any

  constructor(private pricingService: PricingService) { 
  }

  ngOnInit() {
    this.pricingService.getCommodities().then(commodities => {
        this.curves = commodities.curves
    })
  }

  afterChange(changes: any[][]){
    console.log("afterChange called!")
  }

}



