import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule }   from '@angular/forms';
import { RouterModule }   from '@angular/router';
import { AppComponent } from './app.component';
import { AppRoutingModule } from "./app-routing.module";
import { HttpModule }    from '@angular/http';
import { HotTableModule } from 'ng2-handsontable';
import { ApiModule } from "../../generated/ports-distances/api.module";
import { DistanceMatrixComponent } from './distance-matrix/distance-matrix.component';
import { PortsComponent } from './ports/ports.component';
import { PortDetailComponent } from './port-detail/port-detail.component';
import { BASE_PATH } from "../../generated/ports-distances/variables";
import { PricingComponent } from './pricing/pricing.component';
import { ChartModule } from 'primeng/components/chart/chart';
import { HttpClientModule } from '@angular/common/http';
import { CommoditiesComponent } from './pricing/commodities/commodities.component';
import { CurrenciesComponent } from './pricing/currencies/currencies.component';
import { CharterComponent } from './pricing/charter/charter.component';
import { BaseFuelComponent } from './pricing/basefuel/basefuel.component';
import { TabMenuModule } from 'primeng/components/tabmenu/tabmenu';
import { DropdownModule } from 'primeng/components/dropdown/dropdown';
import { ButtonModule } from 'primeng/components/button/button';
import { CommoditiesEditorComponent } from './pricing/commodities/commodities-editor/commodities-editor.component';
import { CurveEditorComponent } from './pricing/curve-editor/curve-editor.component';
import { CommoditiesChartComponent } from './pricing/commodities/commodities-chart/commodities-chart.component';
import { CurrenciesChartComponent } from './pricing/currencies/currencies-chart/currencies-chart.component';
import { CurrenciesEditorComponent } from './pricing/currencies/currencies-editor/currencies-editor.component';


export function getParameterByName(name) {
  var match = RegExp('[?&]' + name + '=([^&]*)').exec(window.location.hash);
  return match && decodeURIComponent(match[1].replace(/\+/g, ' '));
}

export function ApiBaseUrlFactory() {
  if (location.hash.includes('apiBaseUrl')){
    return getParameterByName('apiBaseUrl')
  }
  return "http://localhost:8096"
}

@NgModule({
  declarations: [
    AppComponent,
    DistanceMatrixComponent,
    PortsComponent,
    PortDetailComponent,
    PricingComponent,
    CommoditiesComponent,
    CurrenciesComponent,
    CharterComponent,
    BaseFuelComponent,
    CommoditiesEditorComponent,
    CurveEditorComponent,
    CommoditiesChartComponent,
    CurrenciesChartComponent,
    CurrenciesEditorComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ChartModule,
    FormsModule,
    AppRoutingModule,
    HttpModule,
    HotTableModule,
    ApiModule,
    TabMenuModule,
    DropdownModule,
    ButtonModule,
    HttpClientModule
  ],
  providers: [
    [{ provide: BASE_PATH, useFactory: ApiBaseUrlFactory }] 
  ],
  bootstrap: [AppComponent]
})
export class AppModule {  }



