import { Component, OnInit } from '@angular/core';
import { PortsService, Port, DistancesService } from "../../../generated/ports-distances/index";
import 'rxjs/add/operator/toPromise';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-distance-matrix',
  templateUrl: './distance-matrix.component.html',
  styleUrls: ['./distance-matrix.component.css']
})
export class DistanceMatrixComponent implements OnInit {

  private data: any = new Array();
  private colHeaders: string[] = new Array();
  private columns: any[];
  private options: any;
  private ports: Port[] = new Array();
  private distances: any;
  private version: string;

  // name --> mmxId
  private portMap: Map<string, string> = new Map<string, string>();

  constructor(
    private route: ActivatedRoute,
    private portsService: PortsService,
    private distancesService: DistancesService
  ) {
    if (route.snapshot.paramMap.has("version")){
      this.version = route.snapshot.paramMap.get("version");
    }
    this.options = {
      height: 700,
      rowHeaders: new Array(),
      rowHeaderWidth: 180,
      stretchH: 'all',
      columnSorting: true,
      contextMenu: true,
      className: 'htCenter htMiddle',
      afterChange: (changes) => {
        if (changes == null){
          // prevent update calls when building table
          return
        }
        this.updateDistance(
          this.portForRow(changes[0][0]),
          this.portForColumn(changes[0][1]),
          changes[0][3]
        )
      }
    };
   }

  ngOnInit() {
    this.fetchUpstream()
  }

  private fetchUpstream(){
    this.portsService.getPortsUsingGET(this.version, undefined).toPromise()
    .then(ports => {
      this.ports = ports.filter(p => p.virtual == false)
      this.data = new Array(this.ports.length)
      for (var _i = 0; _i < this.data.length; _i++) {
        this.data[_i] = new Array(this.ports.length)
    }
      this.ports.filter(p => p.virtual == false).forEach(p => {
        this.portMap.set(p.name, p.mmxId)
        this.colHeaders.push(p.name)
        this.options.rowHeaders.push(p.name)
      })
    })
    .catch(error => console.log(error));

    this.distancesService.getDistanceMatrixUsingGET(undefined, this.version, undefined).toPromise()
    .then(distances => {
      this.distances = distances;
      for (var i = 0; i < this.colHeaders.length; i++) {
        this.data[i] = new Array()
        for (var j = 0; j < this.colHeaders.length; j++) {
          this.data[i][j] = this.distances[this.portMap.get(this.options.rowHeaders[i])][this.portMap.get(this.colHeaders[j])]
        }
      }
    })
  }

  private portForRow(row: Number): Port{
    return this.ports.find(p => p.name === this.options.rowHeaders[row.toString()])
  }

  private portForColumn(column: Number): Port{
    return this.ports.find(p => p.name === this.colHeaders[column.toString()])
  }

  private updateDistance(from: Port, to: Port, distance: number){
    this.distancesService.updateDistanceUsingPUT(from.mmxId, to.mmxId, {distance: distance, provider: "Web UI"}).toPromise()
      .then(result => console.log("successfully updated"))
      .catch(error => console.log(error))
    console.log(`zee update: ${from.name} -> ${to.name}: ${distance}`);
  }

  private filterColumn(searchString: string){
    let filteredPorts = this.ports.filter(p => p.name.toLowerCase().startsWith(searchString.toLowerCase()))
    // let filteredColHeaders = this.colHeaders.filter(e => e.startsWith(searchString))
    let filteredData = new Array()
    
    for (var i = 0; i < this.options.rowHeaders.length; i++) {
      filteredData[i] = new Array()
      for (var j = 0; j < filteredPorts.length; j++) {
        filteredData[i][j] = this.distances[this.portMap.get(this.options.rowHeaders[i])][filteredPorts[j].mmxId]
      }
    }
    this.colHeaders = filteredPorts.map(p => p.name)
    this.data = filteredData
    this.options = Object.assign({}, this.options)
  }

  private filterRow(searchString: string){
    let filteredPorts = this.ports.filter(p => p.name.toLowerCase().startsWith(searchString.toLowerCase()))
    // let filteredColHeaders = this.colHeaders.filter(e => e.startsWith(searchString))
    let filteredData = new Array()
    
    for (var i = 0; i < filteredPorts.length; i++) {
      filteredData[i] = new Array()
      for (var j = 0; j < this.colHeaders.length; j++) {
        filteredData[i][j] = this.distances[filteredPorts[i].mmxId][this.portMap.get(this.colHeaders[j])]
      }
    }
    this.options.rowHeaders = filteredPorts.map(p => p.name)
    this.data = filteredData
    this.options = Object.assign({}, this.options)
  }

  fromChange(seachString: string){
    this.filterRow(seachString)
  }

  toChange(seachString: string){
    this.filterColumn(seachString)
  }
}



