import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

export interface EditorUpdate{
  curveName: string,
  curveDate: string,
  oldValue: number,
  newValue: number
}

@Component({
  selector: 'curve-editor',
  templateUrl: './curve-editor.component.html',
  styleUrls: ['./curve-editor.component.css']
})
export class CurveEditorComponent implements OnInit {

  _curves: any;

  @Input() 
  set curves(curves: any){
    if (curves === undefined) return;
    this.createData(curves);
  }

  /**
   * Only called if there is a change. Null events are swallowed.
   */
  @Output() afterChange: EventEmitter<EditorUpdate> = new EventEmitter();

  get curves(): any{
    return this._curves;
  }

  private data: any = new Array();
  private colHeaders: string[] = new Array();
  private columns: any[];
  private options: any;
  private distances: any;
  private version: string;
  private expressionCurves: any[] = new Array();

  constructor() { 

    this.options = {
      // height: 400,
      rowHeaders: new Array(),
      rowHeaderWidth: 80,
      stretchH: 'all',
      columnSorting: true,
      contextMenu: false,
      className: 'htCenter htMiddle',
      afterChange: (changes) => {
        if (changes == null){
          return;
        }
        console.log(changes)
        changes.forEach(element => {
          this.handleAfterChange(element)
        });
        
       }
    };
  }

  private handleAfterChange(changes: any[]){

    let update  = <EditorUpdate>{
      curveName: this.options.rowHeaders[changes[0]],
      curveDate: this.colHeaders[changes[1]],
      oldValue: changes[2],
      newValue: Number(changes[3])
    }
    console.log(update)
    this.afterChange.emit(update)
  }

  ngOnInit() {
  }

  createData(curves: any[]){

    // create headers based on first curve
    // TODO: check for missmatch
    curves.find(e => e['@class'] === '.DataCurve').curve.forEach(e => {
      this.colHeaders.push(e.date)
    });

    let data = new Array()
    curves.forEach(curve => {
      this.options.rowHeaders.push(curve.name)
      if (curve['@class'] === ".ExpressionCurve") return;
      data.push(curve.curve.map(e => e.value))
    });
    this.data = data

    this.expressionCurves = curves.filter(e => e['@class'] === ".ExpressionCurve");
  }
}



