import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location }                 from '@angular/common';
import { Port, PortsService } from "../../../generated/ports-distances/index";
import 'rxjs/add/operator/switchMap';

@Component({
  selector: 'app-port-detail',
  templateUrl: './port-detail.component.html',
  styleUrls: ['./port-detail.component.css']
})
export class PortDetailComponent implements OnInit {

  ngOnInit(): void {
    this.route.paramMap
      .switchMap((params: ParamMap) => this.portsService.getPortUsingGET(params.get('id')))
      .subscribe(port => this.port = port);
  }
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private portsService: PortsService
  ) { }
  goBack(): void {
    this.location.back();
  }
  save(): void {
    //
  }
  @Input() port: Port;
}


