import { Component, OnInit } from '@angular/core';
import { PortsService, Port } from "../../../generated/ports-distances/index";
import { Router } from "@angular/router";

@Component({
  selector: 'app-ports',
  templateUrl: './ports.component.html',
  styleUrls: ['./ports.component.css'],
  providers: [PortsService]
})
export class PortsComponent implements OnInit {

  ngOnInit(): void {
    this.getPorts();
  }
  constructor(
    private router: Router,
    private portsService: PortsService
  ) { }
  ports: Port[];

  getPorts(): void {
    this.portsService.getPortsUsingGET().toPromise()
      .then(ports => this.ports = ports)
      .catch(error => console.log(error));
  }
  gotoDetail(selectedPort: Port): void {
    this.router.navigate(['/detail', selectedPort.mmxId]);
  }
}



