import { Component, OnInit } from '@angular/core';
import { PricingService } from '../../pricing.service';
import { PricingComponent } from '../pricing.component';

@Component({
  selector: 'app-basefuel',
  templateUrl: './basefuel.component.html',
  styleUrls: ['./basefuel.component.css'],
  providers: [PricingService]
})
export class BaseFuelComponent implements OnInit {

  colorStack: Array<string>
  data: any

  constructor(private pricingService: PricingService) {
    this.data = {
      labels: [],
      datasets: []
  }
   }

  ngOnInit() {
    this.colorStack = PricingComponent.COLORS.slice()
    this.pricingService.getBaseFuel()
    .then(result => {
      console.log(result)
      this.data.labels = result.curves[0].curve.map(e => e.date)
      result.curves.forEach(element => {
        this.data.datasets.push({
          label: element.name,
          data: element.curve.map(e => e.value),
          fill: false,
          borderColor: this.colorStack.pop()
        })
      });
    })
  }

}



